export const useContract = () => {
  const { configs } = useConfigs()
  const getContract = (
    abi: any,
    address: string,
    signer?: ethers.Signer | ethers.providers.Provider
  ) => {
    const signerOrProvider = signer || new JsonRpcProvider(configs.rpc)
    return new ethers.Contract(address, abi, signerOrProvider)
  }

  const getRouterContract = (
    signer?: ethers.Signer | ethers.providers.Provider
  ) => {
    return getContract(RouterAbi, configs.helperContract.utr, signer)
  }

  const getPoolContract = (
    poolAddress: string,
    signer?: ethers.Signer | ethers.providers.Provider
  ) => {
    return getContract(PoolAbi, poolAddress, signer)
  }

  const getTokenInfoContract = () => {
    // TODO: need to use tools to get tokenInfo
    // return getContract(TokenInfoAbi, configs.addresses.tokensInfo)
    return getContract(TokenInfoAbi, ZERO_ADDRESS)
  }

  const getUniV3FactoryContract = () => {
    return getContract(UniV3FactoryAbi, configs.uniswap.v3Factory)
  }
  const getChainLinkPriceFeedContract = (priceFeedAddress: string) => {
    return getContract(ChainLinkPriceFeedAbi, priceFeedAddress)
  }
  const getUniV3PairContract = (pairAddress: string) => {
    return getContract(UniV3Pair, pairAddress)
  }
  const getUniV2PairContract = (pairAddress: string) => {
    return getContract(UniV2Pair.abi, pairAddress)
  }

  return {
    getRouterContract,
    getPoolContract,
    getTokenInfoContract,
    getChainLinkPriceFeedContract,
    getUniV3FactoryContract,
    getUniV3PairContract,
    getUniV2PairContract
  }
}
